<template>
  <div class="products-bar">
      <div class="container">
        <ul class="navbar nav">
          <a v-for="(item, index) in filiter" :key="index"
          :class="{active: active===index}" @click.prevent="scroll(index)">
          <li>{{item.groupname}}</li></a>

          <!-- <a :class="{active: active===0}" @click.prevent="scroll(0)">
            <li>{{product[0].productName}}</li>
          </a>
          <a :class="{active: active===1}" @click.prevent="scroll(1)">
            <li>{{product[1].productName}}</li>
          </a>
          <a :class="{active: active===2}" @click.prevent="scroll(2)">
            <li>{{product[2].productName}}</li>
          </a> -->
        </ul>
      </div>
    </div>
</template>

<script>
export default {
  props: {
    active: {
      type: Number,
      default: 0,
    },
    productList: {
      type: Array,
      default: () => ([
        {
          productName: 'LoRaWAN環境感知感應器集中器(LoRa1485)',
        },
        {
          productName: '土壤感測器組(LoRa2485) / (NB4485)',
        },
        {
          productName: 'LoRaWAN環境感知感應器集中器(LoRa1485)',
        },
      ]),
    },
  },
  data() {
    return {
      product: this.productList,
    };
  },
  computed: {
    filiter() {
      const newlist = [];
      this.product.forEach((item) => {
        if (item.scrollNav) {
          newlist.push(item);
        }
      });
      return newlist;
    },
  },
  methods: {
    scroll(n) {
      this.$emit('scroll', n);
    },
  },
};
</script>

